import React, { useState } from 'react';
import { Resend } from 'resend';
import styles from './ContactForm.module.css';

import { message } from 'antd';

function ContactForm() {
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        message: ''
    });

    const resend = new Resend("re_Yez4hiUE_4XGYyoj9c8auAQGqBZTBf5sc");

    const handleSubmit = async () => {
        try {
            await resend.emails.send({
                "from": "onboarding@resend.dev",
                "to": ["alonsdale@bernoullitechnologies.net"],
                "subject": "Hello World",
                "html": "<p>Congrats on sending your <strong>first email</strong>!</p><hr /><p style=\"color:#898989;font-size:12px;\">2261 Market Street #5039 - San Francisco, CA 94114</p>",
                "text": "Congrats on sending your first email!",
                "bcc": [],
                "cc": []
            });
            message.success('Email sent successfully');
        } catch (error) {
            message.error('Failed to send email');
        }
    };

    return (
        <form onSubmit={handleSubmit} className={styles.form}>
            <div className={styles.inputGroup}>
                <input
                    type="text"
                    placeholder="Name"
                    value={formData.name}
                    onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                    className={styles.input}
                />
            </div>
            <div className={styles.inputGroup}>
                <input
                    type="email"
                    placeholder="Email"
                    value={formData.email}
                    onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                    className={styles.input}
                />
            </div>
            <div className={styles.inputGroup}>
                <textarea
                    placeholder="Message"
                    value={formData.message}
                    onChange={(e) => setFormData({ ...formData, message: e.target.value })}
                    className={styles.textarea}
                ></textarea>
            </div>
            <button type="submit" className={styles.button}>Submit</button>
        </form>
    );
}

export default ContactForm;
